import React, { Component } from "react";
import "./App.css";
import counties from "./counties.json";
import Search from "./components/Search";
import Table from "./components/Table";

class App extends Component {
  state = {
    counties: [],
    filtered: [],
    didSearch: false,
    keyword: ""
  };

  componentDidMount() {
    let mappedCounties = [];
    counties.map(county => {
      county.population = county.population.toLocaleString();
      mappedCounties.push(county);
    });
    this.setState({ counties: mappedCounties, filtered: mappedCounties });
  }

  search = keyword => {
    this.setState({ keyword: keyword });
    let didSearch = false;

    if (keyword) {
      didSearch = true;
    }

    let { counties } = this.state;
    let filteredList = counties.filter(item => {
      // number so do a plate search
      if (isNaN(keyword) === false) {
        let plate = item.plate.toString();
        return plate.toLowerCase().search(keyword.toLowerCase()) !== -1;
      }

      // letter so do a name or seat search
      return (
        item.name.toLowerCase().search(keyword.toLowerCase()) !== -1 ||
        item.seat.toLowerCase().search(keyword.toLowerCase()) !== -1
      );
    });
    this.setState({ filtered: filteredList, didSearch: didSearch });
  };

  clearSearch = () => {
    this.setState({
      didSearch: false,
      filtered: this.state.counties,
      keyword: ""
    });
  };

  render() {
    let { filtered, didSearch, keyword } = this.state;

    return (
      <div className="App">
        <Search
          keyword={keyword}
          parentSearch={this.search}
          didSearch={didSearch}
          parentClearSearch={this.clearSearch}
        />
        <Table counties={filtered} />
      </div>
    );
  }
}

export default App;



