import React, { Component } from "react";

class Search extends Component {
  componentDidMount() {
    this.searchInput.focus();
  }

  handleSearch = e => {
    window.scroll(0, 0);
    let keyword = e.target.value;

    // only allow two numbers to be typed
    if (isNaN(keyword) === false && keyword.length > 2) {
      return e.preventDefault();
    }
    this.props.parentSearch(keyword);
  };

  render() {
    let { parentClearSearch, didSearch, keyword } = this.props;
    return (
      <div className="search">
        {didSearch && (
          <span className="clear" onClick={parentClearSearch}>
            &times;
          </span>
        )}
        <img src="search-icon.png" className="search-icon" />
        <input
          autoFocus={true}
          type="tel"
          name="keyword"
          value={keyword}
          placeholder="Search by plate number..."
          onChange={this.handleSearch}
          ref={c => (this.searchInput = c)}
        />
      </div>
    );
  }
}

export default Search;



