import React from "react";
import { withStyles } from "@material-ui/core/styles";

const styles = {
  iframe: {
    width: "100%",
    height: 400,
    border: "1px solid #ddd",
    marginTop: 20
  }
};

const GoogleMap = ({ classes, city }) => {
  let mapSrc =
    "//maps.google.com/maps?q=" +
    city +
    ",Nebraska" +
    "&t=&z=13&ie=UTF8&iwloc=&output=embed&zoom=400";

  return (
    <iframe className={classes.iframe} width="600" height="450" src={mapSrc} />
  );
};

export default withStyles(styles)(GoogleMap);



