import React, { Component } from "react";
import GoogleMap from "./GoogleMap";

class CountyPopUp extends Component {
  state = {
    animation: "slideInUp",
    loadingMap: true
  };

  componentDidMount() {
    window.setTimeout(() => {
      this.setState({ loadingMap: false });
    }, 2000);
  }

  closePopup = () => {
    this.setState({ animation: "slideOutDown" });

    // let the popup close first before calling the callback
    window.setTimeout(() => {
      this.props.parentClosePopUp();
    }, 1000);
  };

  render() {
    let { animation, loadingMap } = this.state;
    let { county } = this.props;
    let countyImg = "counties/" + county.name + ".png";

    return (
      <div className={"animated popup faster " + animation}>
        <div className="container">
          <div className="close" onClick={this.closePopup}>
            &times;
          </div>
          <h1>
            {county.name} County ({county.plate})
          </h1>
          <h2>
            {county.population} / {county.seat}
          </h2>
          <hr />
          <img src={countyImg} className="county-image" />
          {loadingMap && <div className="loading" />}
          <GoogleMap city={county.seat} />
        </div>
      </div>
    );
  }
}
export default CountyPopUp;



