import React, { Component } from "react";
import ReactTable from "react-table";
import "react-table/react-table.css";
import CountyPopUp from "./CountyPopUp";

const columns = [
  {
    Header: "#",
    accessor: "plate",
    width: 50,
    style: {
      textAlign: "center"
    }
  },
  {
    Header: "Name",
    accessor: "name"
  },
  {
    Header: "Seat",
    accessor: "seat"
  },
  {
    Header: "Pop",
    accessor: "population",
    width: 86,
    style: {
      textAlign: "center"
    }
  }
];

class Table extends Component {
  state = {
    county: null
  };

  onRowClick = county => {
    this.setState({ county: county });
  };

  parentClosePopUp = () => {
    this.setState({ county: null });
  };

  render() {
    let { counties } = this.props;
    let { county } = this.state;
    let numRows = counties.length ? counties.length : 93;

    return (
      <div className="table">
        {county && (
          <CountyPopUp
            county={county}
            parentClosePopUp={this.parentClosePopUp}
          />
        )}
        <ReactTable
          defaultPageSize={numRows}
          showPagination={false}
          data={counties}
          columns={columns}
          getTdProps={(state, rowInfo, column, instance) => {
            return {
              onClick: (e, handleOriginal) => {
                this.onRowClick(rowInfo.original);
              }
            };
          }}
        />
      </div>
    );
  }
}

export default Table;



